const base64 = require('js-base64').Base64;
const operator = 'youplus'
const password = 'youplus123456'
const authorInfo = base64.encode(operator + ':' + password)
const upyun = {
    url: 'http://v0.api.upyun.com/youplus/',
    headers: {
        Authorization: authorInfo
    }
}
export default upyun